import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import Start from './page/Start';
import Person from './page/Person';
import Page01 from './page/Page01';
import Page02 from './page/Page02';
import Page03 from './page/Page03';
import Page04 from './page/Page04';
import Page05 from './page/Page05';
import Page06 from './page/Page06';
import Page07 from './page/Page07';
import Page08 from './page/Page08';
import Page09 from './page/Page09';
import Page091 from './page/Page09-1';
import Page10 from './page/Page10';
import Page11 from './page/Page11';
import Page12 from './page/Page12';
import Page13 from './page/Page13';
import Page14 from './page/Page14';
import Page15 from './page/Page15';
import Page151 from './page/Page15-1';
import Page16 from './page/Page16';
import Page161 from './page/Page16-1';
import Complete from './page/Complete';

function App() {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <div>
                <Switch>
                    <Route exact path="/" component={Start} />
                    <Route path="/person" component={Person} />
                    <Route path="/page1" component={Page01} />
                    <Route path="/page2" component={Page02} />
                    <Route path="/page3" component={Page03} />
                    <Route path="/page4" component={Page04} />
                    <Route path="/page5" component={Page05} />
                    <Route path="/page6" component={Page06} />
                    <Route path="/page7" component={Page07} />
                    <Route path="/page8" component={Page08} />
                    <Route path="/page9" component={Page09} />
                    <Route path="/page10" component={Page091} />
                    <Route path="/page11" component={Page10} />
                    <Route path="/page12" component={Page11} />
                    <Route path="/page13" component={Page12} />
                    <Route path="/page14" component={Page13} />
                    <Route path="/page15" component={Page14} />
                    <Route path="/page16" component={Page15} />
                    <Route path="/page17" component={Page151} />
                    <Route path="/page18" component={Page16} />
                    <Route path="/page19" component={Page161} />
                    <Route path="/complete" component={Complete} />
                </Switch>
            </div>
        </Router>
    );
}

export default App;
