import React from 'react';
import { Col, Row, ProgressBar } from 'react-bootstrap';

export default function ProgressTop(props) {
    const part1 = props.part === '1' ? 9 : 1;
    const part2 = props.part === '2' ? 9 : 1;
    const part3 = props.part === '3' ? 9 : 1;
    const part4 = props.part === '4' ? 9 : 1;
    return (
        <Col xs={12} className="pb10">
            <Row>
                <Col xs={12} className="pl5 pr5 pt10 pb10">
                    <ProgressBar
                        now={props.now}
                        key={1}
                        label={`${props.now}%`}
                    />
                </Col>
                <Col xs={part1}>
                    <span>Ⅰ.{props.part === '1' && ' 응답자 인적사항'}</span>
                </Col>
                <Col xs={part2}>
                    <span>
                        Ⅱ.{props.part === '2' && ' 구직성향에 대한 조사'}
                    </span>
                </Col>
                <Col xs={part3}>
                    <span>
                        Ⅲ.
                        {props.part === '3' &&
                            ' 충북지역 기업에 대한 인식 조사'}
                    </span>
                </Col>
                <Col xs={part4} className="text-right">
                    <span>
                        Ⅳ.
                        {props.part === '4' &&
                            ' 취업에 임하는 대학생의 직업관에 대한 조사'}
                    </span>
                </Col>
            </Row>
        </Col>
    );
}
